<template>
  <v-sheet>
    <v-sheet class="text-center " color="orange lighten-3" style="padding-bottom: 150px">

      <v-row>
        <v-spacer></v-spacer>
        <v-col cols="11">
          <v-sheet class="d-flex text-h4 z-transparent mt-2">
            ADASD: The Activity-Dependent Alternative Splicing Database for the Brain
          </v-sheet>
          <v-card class="mt-6 pa-2 " elevation="8" min-height="280">
            <v-row>
              <v-col cols="12" lg="7" md="12" sm="12" xl="7">
                <v-row>
                  <v-col cols="12">
                    <v-card elevation="0" style="text-align: left">
                      <v-card-subtitle>
                        <v-icon>mdi-turbine</v-icon>
                        About
                      </v-card-subtitle>
                      <v-card-text class="text-justify">
                        The Activity-Dependent Alternative Splicing Database for the Brain (ADASD) is a web-based platform that provides access to comprehensive annotations of activity-dependent alternative splicing patterns, as well as protein domain information for human and mouse genes in the brain. The database includes five high-quality, deep-sequencing RNA-seq and scRNA-seq datasets from different brain regions under various conditions. It offers robust evidence and reference for activity-dependent alternative splicing at both in vivo and in vitro levels.
                      </v-card-text>
                    </v-card>
                  </v-col>

                  <v-col cols="12">
                    <v-card elevation="0">
                      <v-card-subtitle class="text-left  text-body-2">
                        <v-icon small>mdi-information-outline</v-icon>
                        To get the best experience, please visit us using the latest version of Google Chrome or
                        Microsoft Edge.
                      </v-card-subtitle>

                    </v-card>
                  </v-col>

                </v-row>
              </v-col>
              <v-col cols="12" lg="5" md="12" sm="12" xl="5">
                <v-row>
                  <v-col cols="12">
                    <v-card elevation="0" style="text-align: left">
                      <v-card-subtitle>
                        <v-icon>mdi-chart-bar-stacked</v-icon>
                        Statistic
                      </v-card-subtitle>

                      <v-card-text style="min-height: 250px">

                        <v-row>
                          <v-spacer></v-spacer>
                          <v-col cols="11">
                            <v-row>
                              <v-col class="pa-0"  v-for="item in stats.data" :key="item.name" cols="12" lg="4" md="4" sm="4" xl="4">
                                <v-hover v-slot="{ hover }">
                                  <v-card
                                      :elevation="hover?4:0"
                                      :to="item.to"
                                      class="ma-1 d-flex justify-center "
                                      height="100"
                                      tile
                                  >

                                    <v-sheet class="pt-6 ">
<!--                                    <v-sheet class="px-auto ">-->
                                      <v-progress-circular
                                          v-show="stats.isShow==null"
                                          :color="$store.state.mainColor"
                                          :size="45"
                                          :width="7"
                                          indeterminate

                                      ></v-progress-circular>

                                      <div v-show="stats.isShow !=null" :style="{color: $store.state.mainColor}"
                                           class="text-h6"
                                      >
                                        <v-icon :color="item.color">{{ item.icon }} mdi-36px</v-icon>
                                        {{ item.num }}
                                      </div>
                                      <v-divider dark></v-divider>

                                      <div class="text-center"   :style="{color: $store.state.mainColor}"
                                           v-html="item.name"></div>
                                    </v-sheet>

                                  </v-card>
                                </v-hover>
                              </v-col>
                            </v-row>
                          </v-col>
                          <v-spacer></v-spacer>
                        </v-row>

                      </v-card-text>
                    </v-card>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
        <v-spacer></v-spacer>
      </v-row>
    </v-sheet>
    <v-sheet class="text-center " color="amber  lighten-3">
      <v-row>
        <v-spacer></v-spacer>
        <v-col cols="11">
          <v-row dense>

            <v-col cols="12" lg="3" md="6" sm="12" xl="3">
              <v-card :style="{marginTop: changeOffset($vuetify.breakpoint.name,isTop=false)}" elevation="8"
                      height="300">
                <v-card-subtitle class="text-left">
                  <v-icon>mdi-cog-outline</v-icon>
                  Alternative Splicing Event
                </v-card-subtitle>
                <v-card-text style="height: 250px">
                  <v-img contain  height="100%"  :src="require('../../public/imgs/alternative_splicing_events.png')" ></v-img>
                </v-card-text>
              </v-card>
            </v-col>
            <v-col cols="12" lg="3" md="6" sm="12" xl="3">
              <v-card :style="{marginTop: changeOffset($vuetify.breakpoint.name,isTop=true,isTop2=true)}" elevation="8"
                      height="300">
                <v-card-subtitle class="text-left">
                  <v-icon>mdi-newspaper-variant-multiple-outline</v-icon>
                  Updates
                </v-card-subtitle>
                <v-card-text>
                  <v-sheet class="overflow-y-auto mx-2" height="220">
                    <v-timeline align-top dense>
                      <v-timeline-item v-for="(item,idx) in news" :key="idx" color="orange lighten-1" fill-dot
                                       icon="mdi-star" small>
                        <v-row class="mr-2" justify="space-between">
                          <v-col class="text-left" cols="12">
                            {{ item.date }} &nbsp;
                            {{ item.text }}
                          </v-col>
                        </v-row>
                      </v-timeline-item>

                    </v-timeline>
                  </v-sheet>
                </v-card-text>
              </v-card>
            </v-col>
            <v-col cols="12" lg="3" md="6" sm="12" xl="3">
              <v-card :style="{marginTop: changeOffset($vuetify.breakpoint.name,isTop=true,isTop2=false)}" elevation="8"
                      height="300">
                <v-card-subtitle class="text-left">
                  <v-icon>mdi-earth</v-icon>
                  Contact
                </v-card-subtitle>
                <v-card-text class="text-left">
                  Xiao Han,Ph.D.
                  <br/>
                  <a href="hanxiao3@stanford.edu" target="_blank">hanxiao3@stanford.edu </a>
                </v-card-text>
              </v-card>
            </v-col>

            <v-col cols="12" lg="3" md="6" sm="12" xl="3">
              <v-card :style="{marginTop: changeOffset($vuetify.breakpoint.name,isTop=false)}" elevation="8"
                      height="300">
                <v-card-subtitle class="text-left">
                  <v-icon>mdi-link-variant</v-icon>
                  Related Resources
                </v-card-subtitle>
                <v-card-text>
                  <v-list class="overflow-y-auto overflow-x-hidden" max-height="220">
                    <v-list-item v-for="(item,idx) in relatedlinks" :key="idx" :href="item.href" class="align--center"
                                 target="_blank">
                      <v-sheet class="text-left align--center">
                        <v-icon small>mdi-link-variant</v-icon> &nbsp;
                        <span class="black--text text-body-2 font-weight-bold mr-3" style="white-space: nowrap">{{
                            item.name
                          }}</span>

<!--                        <img :src="item.logo" height="20"-->
<!--                             style="display: inline-block;border: 1px solid darkgrey"/>-->
                      </v-sheet>
                    </v-list-item>
                  </v-list>
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
          <v-row dense>
            <v-col cols="12">
              <v-card elevation="8">
                <v-card-subtitle class="text-left">
                  <v-icon>mdi-format-quote-open-outline</v-icon>
                  Citations
                </v-card-subtitle>
                <v-card-text class="text-left">
                  <v-icon small>mdi-format-quote-open-outline</v-icon>

                  Chowdhury, M., Pedersen, B.S., Sedlazeck, F.J. et al. Searching thousands of genomes to classify somatic and novel structural variants using STIX. Nat Methods 19, 445–448 (2022). DOI:<a href="https://www.nature.com/articles/s41592-022-01423-4" target="_blank">10.1038/s41592-022-01423-4</a>
                  <br/>
                  <v-icon small>mdi-format-quote-open-outline</v-icon> Xinchang Zheng, Murad Chowdhury, Behzod Mirpochoev, Aaron Clauset, Ryan M Layer, Fritz J Sedlazeck. STIX: Long-reads based Accurate Structural Variation Annotation at Population Scale. BioRxiv DOI: <a href="https://www.biorxiv.org/content/10.1101/2024.09.30.615931v1" target="_blank">10.1101/2024.09.30.615931v1</a>


                </v-card-text>

              </v-card>
            </v-col>
          </v-row>

        </v-col>
        <v-spacer></v-spacer>
      </v-row>
    </v-sheet>
  </v-sheet>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      news: [
        {
          date: "[2024/05/11]",
          text: 'r sit amet, consectetur a'
        },
        {
          date: "[2023/04/11]",
          text: 'corporis cumque debitis delectus earum '
        },
      ],
      relatedlinks: [
        {
          href: "https://med.stanford.edu/sudhoflab.html",
          icon: "mdi-link-variant",
          name: "Thomas C. Südhof Lab",
        },

      ],
      stats:{
        data:[
          {
            to:"#",
            name:"Tissue",
            icon: "mdi-water",
            color:"red lighten-3",
            num:3485,
            fontsize:12,
          },
          {
            to:"#",
            name:"Cells",
            icon: "mdi-dots-circle",
            color:"cyan lighten-3",
            num:22382,
            fontsize:12,
          },
          {
            to:"#",
            name:"AS Event",
            icon: " mdi-flash",
            color:"blue lighten-3",
            num:6983,
            fontsize:12,
          },
          {
            to:"#",
            name:"Gene",
            icon: "mdi-dna",
            color:"green lighten-3",
            num:3485,
            fontsize:12,
          },
          {
            to:"#",
            name:"publication",
            icon: "mdi-book",
            color:"orange lighten-3",
            num:22382,
            fontsize:12,
          },
          {
            to:"#",
            name:"AS Event",
            icon: " mdi-flash",
            color:"blue lighten-3",
            num:6983,
            fontsize:8,
          },

        ],
        isShow:null
      }
    }
  },
  methods: {
    changeOffset(breakpointName, isTop = false, isTop2 = false) {
      console.log(breakpointName)
      switch (breakpointName) {
        case 'lg':
          return '-120px'
        case 'xl':
          return '-120px'
        case 'xs':
          if (isTop2) {
            return '-120px'
          } else {
            return '12px'
          }
        case 'sm':
          if (isTop2) {
            return '-120px'
          } else {
            return '12px'
          }
        default:
          if (isTop) {
            return '-120px'

          } else {
            return '12px'
          }
      }

      // (( $vuetify.breakpoint.name == 'lg' ||  $vuetify.breakpoint.name == 'xl') ? '-120px' : '12px')
    }
  },
  mounted() {

    setTimeout(()=>{
      this.stats.isShow=true;
    },1000)
  }
}
</script>

<style lang="scss" scoped>

.z-transparent {
  background-color: transparent !important;
  opacity: 2;
  border-color: transparent !important;
}

//$btn-text-transform: none;
.v-btn {
  text-transform: capitalize;
}

@import "~vuetify/src/components/VBtn/_variables.scss";

</style>